import {Box,Spacer,Heading,Flex,ButtonGroup,Text,Button,Container, Input,Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton} from "@chakra-ui/react"
import React, { useState } from "react"
import { useDisclosure, } from "@chakra-ui/react"
import { useMediaQuery } from "@chakra-ui/react"
  


export default function Navbar({handleClick}){
  const [isMobile] = useMediaQuery("(max-width: 768px)") 
  const { isOpen, onOpen, onClose } = useDisclosure()
  const btnRef = React.useRef()
  return  (isMobile?<> 
  <Flex><Spacer></Spacer><Button ref={btnRef} colorScheme='teal'  onClick={onOpen}>
  <i class="fa-solid fa-bars"></i>
</Button></Flex>
 <Drawer
   isOpen={isOpen}
   placement='right'
   onClose={onClose}
   finalFocusRef={btnRef}
 >
   <DrawerOverlay />
   <DrawerContent>
    <DrawerCloseButton />
    <DrawerBody>
   <Text  onClick={onClose}>Home</Text>
   <Text  onClick={onClose}>About</Text>
   <Text  onClick={onClose}>Work</Text>
   <Text  onClick={onClose}>Skill</Text>
   <Text onClick={onClose}>Contact</Text>
     </DrawerBody>
    
   </DrawerContent>
 </Drawer></>: <Container maxWidth={"container.xl"} ><Flex minWidth='max-content'  alignItems='center' gap='2'>
   <Box p='2'>
     <Heading size='md' style={{fontFamily:"BestermindRegular"}} color={"white"}>Sudarshan</Heading>
   </Box>
   <Spacer />
   <ButtonGroup color={"white"} gap='2'>
     <Text onClick={handleClick}>Home</Text>
     <Text onClick={handleClick}>About</Text>
     <Text onClick={handleClick}>Work</Text>
     <Text onClick={handleClick}>Skill</Text>
     <Text onClick={handleClick}>Contact</Text>
   </ButtonGroup>
     </Flex>
 </Container>)
}


