import './App.css';
import React, { useRef } from 'react';
import Navbar from './components/Navbar';
import Home from './pages/Home';
import About from './pages/About';


function App() {
  const aboutsec=useRef(null);
  const handleClick=()=>{
    window.scrollTo({
      top:aboutsec.current.offsetTop,
      behavior:"smooth"
    })
  }
  return (
    <div className="App" style={{backgroundImage: "url(https://th.bing.com/th/id/OIP.nFWWtbD7hSMBTyrbLvDbcwHaEK?pid=ImgDet&rs=1)"} }>
     <Navbar handleClick={handleClick}></Navbar>
     <Home aboutsec={aboutsec}></Home>
     <About aboutsec={aboutsec}></About>
     
     
    
     </div>
  );
}

export default App;
